<template lang="pug">
    div.content
        h3 支付100.00
        div.btn-group
            span.btn-pay(@click="weixinPay()") 确认支付
</template>
<script>
export default {
    methods: {
        weixinPay: function(data) {
            var self = this;
            if (typeof WeixinJSBridge == "undefined") {//微信浏览器内置对象。参考微信官方文档
                if (document.addEventListener) {
                    document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady(data), false);
                } else if (document.attachEvent) {
                    document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady(data));
                    document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady(data));
                }
            } else {
                self.onBridgeReady(data);
            }
        },

        /**
         * @method 支付费用方法
         * @param data:后台返回的支付对象,(详情微信公众号支付API中H5提交支付);
         */
        onBridgeReady: function(data) {
            var self = this;
            WeixinJSBridge.invoke(
                'getBrandWCPayRequest', {
                    "appId": data.appId, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                    "timeStamp": data.timeStamp, //时间戳
                    "nonceStr": data.nonceStr, // 支付签名随机串，不长于 32 位
                    "package": data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
                    "signType": data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                    "paySign": data.paySign // 支付签名
                },
                function(res) {
                    // 支付成功
                    if (res.err_msg == "get_brand_wcpay_request：ok") {
                        Toast('支付成功');
                    }
                    // 支付失败
                    else {
                        alert("支付失败,请跳转页面" + res.err_msg);
                    }
                }
            );
        }
    }
}
</script>
<style lang="less" scoped>
    @import '../../assets/css/variable.less';
    h3{
        font-size: 1.6rem;
        text-align: center;
    }
    .btn-group{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .btn-pay{
        height: 2rem;
        line-height: 2rem;
        display: inline-block;
        background-color: @color-theme;
        color: #fff;
        border-radius: 2rem;
        padding: 0 1rem;
    }
</style>




